.page-header {
  width: 100%;
  &__contacts{
  	background-color: #e9e9f1;
  }

  &__container{
  	margin: 0 auto;
  	padding: 0 30px;
  	overflow: hidden;
  	}
  
}

.contacts{
	display: inline-block;
	float: right;

		&__time{
			display: inline-block;
		}
		&__phone{
			display: inline-block;
		}
		
		&__location{
			display: inline-block;
		}
}
